import React, { useState } from 'react'


export default function TextForm(props) {
    const handelUpClick = () => {
        let newText = text.toUpperCase();
        setText(newText);
        props.showAlert("Converted to uppercase!", "success");
    }
    const handelOnChange = (event) => {
        setText(event.target.value);
        // let  newWord;
        // let  texts ;
        // if(text.length !== 0){
        //   texts = text.split(/[ ]+/).length;
        //     if(texts.charAt(texts.length-1) === ' '){
        //         newWord = texts.length - 1;
        //     }else{
        //         newWord = texts.length;
        //     }
        // }else{
        //     newWord = 0;
        // }
    }
    const handelLoClick = () => {
        let newText = text.toLowerCase();
        setText(newText);
        props.showAlert("Converted to lowercase!", "success");
    }
    const [myFont, setMyFont] = useState({
        fontFamily: 'Arial',
        fontSize: '14px'
    })
    const [btntext, setBtnText] = useState("Ephesis")
    const handelCursiveClick = () => {
        if (myFont.fontFamily === 'Arial') {
            setMyFont({
                fontFamily: "'Ephesis'",
                fontSize: '24px'
            })
            setBtnText("Arial");
            props.showAlert("Preview font changed to Ephesis", "success");
        }
        else {
            setMyFont({
                fontFamily: 'Arial',
                fontSize: '14px'
            })
            props.showAlert("Preview font changed to Arial", "success");
            setBtnText("Ephesis")
        }
    }
    const handelClearClick = () => {
        let newText = '';
        setText(newText);
        props.showAlert("Text cleared!", "success");
    }
    const handelCopy = () => {
        let text = document.getElementById("myBox");
        text.select();
        navigator.clipboard.writeText(text.value);
        props.showAlert("Copied to Clipboard", "success");
    }
    const handelExtraSpaces = () => {
        let newText = text.split(/[ ]+/);
        setText(newText.join(" "));
        props.showAlert("Extra spaces removed!", "success");
    }

    const [text, setText] = useState('');
    return (
        <>
            <div className="container" style={{ color: props.mode === 'dark' ? 'white' : 'black' }}>
                <h1>{props.heading}</h1>
                <div className="mb-3">
                    <textarea className="form-control" style={{ backgroundColor: props.mode === 'dark' ? '#191929' : 'white', color: props.mode === 'dark' ? 'white' : 'black' }} placeholder="Enter text here" value={text} onChange={handelOnChange} id="myBox" rows="8"></textarea>
                </div>
                <button className="btn btn-primary mx-2 my-2" onClick={handelUpClick}>Convert to Uppercase</button>
                <button className="btn btn-primary mx-2 my-2" onClick={handelLoClick}>Convert to Lowercase</button>
                <button className="btn btn-primary mx-2 my-2" onClick={handelClearClick}>Clear</button>
                <button className="btn btn-primary mx-2 my-2" onClick={handelCursiveClick}>{btntext}</button>
                <button className="btn btn-primary mx-2 my-2" onClick={handelCopy}>Copy Text</button>
                <button className="btn btn-primary mx-2 my-2" onClick={handelExtraSpaces}>Remove Extra Spaces</button>
            </div>
            <div className="container my-3" style={{ color: props.mode === 'dark' ? 'white' : 'black' }}>
                <h2>Your text summary</h2>
                {/* <p>{text.charAt(text.length-1) === ' '?text.split(/[ ]+/).length:text.split(" ").length} words and {text.length} characters</p> */}
                <p>{text.length > 0 ? text.length : 0} words and {text.length} characters</p>
                <p>{text.length > 0 ? 0.008 * text.split(" ").length : 0} MInutes read</p>
                <h2>Preview</h2>
                <p style={myFont}>{text.length > 0 ? text : "Enter somthing in the textbox above to preview it here."}</p>
            </div>
        </>
    );
}
